import MyReact from './MyReact'

const MyReactDOM = {
    render,
    setAttribute,
    // renderComponent
}

function render(vnode,container) {
    // console.log(vnode)
    if(!vnode) return

    if(typeof vnode === 'string' || typeof vnode === 'number') {
        let dom = document.createTextNode(vnode)
        container.append(dom)
        return
    } else if(Array.isArray(vnode)) {
        let dom = document.createDocumentFragment()
        vnode.forEach( child => {
            // console.log(child)
            render(child,dom) //é€’å½’æ¸²æŸ“
        })
        container.append(dom)
    } else if(typeof vnode === 'object') {
        let dom,child
        if( typeof vnode.tag === 'function') {
            // console.log(vnode.tag)  //æ˜¯ä¸€ä¸ªæ„é€ å‡½æ•°ï¼Œåˆ™åˆ›å»ºä¸€ä¸ªç»„ä»¶
            dom = document.createDocumentFragment()
            child = createVnodeFromComponent(vnode.tag,vnode.attrs)  //{}
            render(child,dom)
        } else {
            dom = document.createElement(vnode.tag)
            setAttribute(dom,vnode.attrs)
            if(Array.isArray(vnode.children) && vnode.children.length > 0) {
                vnode.children.forEach( child => {
                    //é€’å½’
                    render(child,dom)
                })
            }
        }
        container.append(dom)
    }
}

// ä»ç»„ä»¶ä¸­åˆ›å»ºè™šæ‹ŸDOM
function createVnodeFromComponent(constructor,attrs) {
    let component,vnode
    if(constructor.prototype instanceof MyReact.Component) {  //classç»„ä»¶
        component = new constructor(attrs)
        vnode = component.render(attrs) //å¾—åˆ°vnode
        // console.log(attrs)
    } else {  //å‡½æ•°ç»„ä»¶
        // console.log(attrs)
        component = new MyReact.Component(attrs)
        component.constructor = constructor //æ„é€ å‡½æ•°é‡æ–°æŒ‡å‘
        component.render = function () {
            return this.constructor(attrs)
        }
        vnode = component.render(attrs)
    }
    return vnode

}


function renderComponent(component) {
    
}


//ç»™DOMå…ƒç´ è®¾ç½®å±æ€§
function setAttribute(dom, attrs) {
    if (!attrs) {
        return
    }
    for (let key in attrs) {
        if (/^on/.test(key)) {
            dom[key.toLowerCase()] = attrs[key]
        } else if (key === 'style') {
            //è¿™æ ·ç›´æ¥èµ‹å€¼çš„è¯ï¼Œstyleå±æ€§å’Œonclickäº‹ä»¶å°±æ²¡æ³•ç»‘å®š,éœ€è¦åšä¸€ä¸ªåˆ¤æ–­
            Object.assign(dom.style, attrs[key])
        } else {
            dom[key] = attrs[key]
        }
    }
}


export default MyReactDOM


